<template>
  <div id="app">
    <div class="imgBox" v-if="Img">
      <img :src="GetSrc" alt="" style="width: 98%; margin: 0 auto" />
    </div>
    <div style="width: 100%">{{ GetSrc }}</div>
    <div class="btn">
      <van-button type="primary" @click="start">开始</van-button>
      <van-button type="info" @click="stop">结束</van-button>
    </div>
    <div></div>
  </div>
</template>

<script>
import AMapLoader from "@amap/amap-jsapi-loader";
import Vue from "vue";
import { Button } from "vant";
Vue.use(Button);
export default {
  name: "App",
  data() {
    return {
      time: "",
      ArrList: [],
      urlStr: "",
      Img: false,
      imgSrc: "",
    };
  },
  computed: {
    GetSrc: function () {
      return (
        "https://restapi.amap.com/v3/staticmap?zoom=13&size=1024*1024&paths=10,0x0000ff,1,,:" +
        this.urlStr +
        "&key=66fa6bc02423ca698b2faaca96e2e1f6"
      );
    },
  },
  components: {},
  methods: {
    mapInit() {
      let _this = this;
      AMapLoader.load({
        key: "6227aa1c7c159e933b07da0d4114028b",
        version: "2.0",
      })
        .then((AMap) => {
          AMap.plugin("AMap.Geolocation", function () {
            var geolocation = new AMap.Geolocation({
              // 是否使用高精度定位，默认：true
              enableHighAccuracy: true,
              // 设置定位超时时间，默认：无穷大
              timeout: 10000,
              // 定位按钮的停靠位置的偏移量，默认：Pixel(10, 20)
              buttonOffset: new AMap.Pixel(10, 20),
              //  定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
              zoomToAccuracy: true,
              //  定位按钮的排放位置,  RB表示右下
              buttonPosition: "RB",
            });
            geolocation.getCurrentPosition();
            geolocation.getCurrentPosition(function (status, result) {
              if (status == "complete") {
                onComplete(result);
              } else {
                onError(result);
              }
            });
            function onComplete(data) {
              // data是具体的定位信息
              let lat = data.position.lat;
              let lng = data.position.lng;
              let arr = [lng, lat];
              _this.ArrList.push(arr);
              console.log(_this.ArrList);
            }
            function onError(data) {
              // 定位出错
              console.log(data);
            }
          });
        })
        .catch((e) => {
          console.log(e);
        });
    },
    start() {
      console.log("开始定位");
      this.time = setInterval(() => {
        this.mapInit();
      }, 1000);
    },
    stop() {
      console.log("结束定位");
      let _this = this;
      clearInterval(this.time);
      // 请求静态地图接口
      _this.Img = true;
      console.log(_this.ArrList);
      let Str = _this.ArrList.join(";");
      this.urlStr = Str;
    },
  },
};
</script>

